.page-header {
  position: relative;
  z-index: 11;

  color: #ffffff;

  background-color: @dark-blue;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.page-header__form {
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}

.page-header__background {
  position: relative;
  background: none;

  &--white {
    background-color: #ffffff;
  }
}

.page-header__wrapper {
  position: relative;
  z-index: 11;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;

  &--index {
    padding-bottom: 45px;
  }
}

.page-header-list {
  display: none;
}

.page-header__logo {
  display: flex;
}

.page-header__picture {
  display: none;
}

.page-header__picture--opener {
  display: block;
}

.page-header__toggle {
  font-size: 0;

  background: transparent;
  border: none;
}

.page-header__toggle--closed {

  width: 25px;
  height: 11px;

  background: url("../img/menu.svg") no-repeat center;
}

.page-header__toggle--opened {
  width: 19px;
  height: 19px;

  background: url("../img/menu-close.svg") no-repeat center;
}

.page-header__title {
  position: relative;
  display: inline;

  width: 100%;
  margin: 0;
  padding-top: 55px;
  padding-bottom: 5px;

  font-family: "BloggerSans-Bold", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 800;
  line-height: 40px;

  &::after {
    content: "";

    position: absolute;
    left: 0;
    bottom: -5px;

    width: 172px;
    height: 5px;

    background-color: @orange;
    border-radius: 5px;
  }
}

.page-header__title--form {
  &::after {
    width: 214px;
  }
}

.page-header__link {
  display: none;
}

@media (min-width: @tablet-width) {
  .page-header {
    background-color: #182044;
  }

  .page-header__form {
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;
  }

  .page-header__title {
    order: 4;
    width: 60%;
    margin-top: 72px;
    padding-bottom: 60px;
    padding-left: 30px;


    font-size: 60px;
    line-height: 70px;

    &::after {
      left: 30px;

      width: 287px;
    }

    &--form {
      &::after {
        width: 360px;
      }
    }
  }

  .page-header__link {
    position: relative;

    display: block;
    order: 5;
    margin-top: 75px;
    padding-right: 100px;

    font-family: "BloggerSans-Bold", "Arial", sans-serif;
    font-size: 20px;
    font-weight: 800;
    line-height: 24px;
    text-decoration: none;
    text-transform: uppercase;
    color: #ffffff;

    &:hover,
    &:focus {
      color: fade(#ffffff, 60%);
    }

    &:active {
      color: fade(#ffffff, 30%);
    }

    &::after {
      content: "";

      position: absolute;
      top: 4px;
      right: 35px;

      width: 32px;
      height: 16px;

      background: url("../img/return.svg") no-repeat center;
    }

    &:hover::after,
    &:focus::after {
      opacity: 0.6;
    }

    &:active::after {
      opacity: 0.3;
    }
  }

  .page-header__wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;

    &--index {
      padding-bottom: 70px;
    }
  }

  .page-header__logo {
    order: 1;
    align-self: flex-start;
  }

  .page-header__toggle {
    order: 2;
    margin-left: 22px;
  }

  .page-header-list {
    top: 0;

    display: flex;
    order: 3;

    .intro-nav__item:nth-child(1),
    .intro-nav__item:nth-child(2) {
      display: none;
    }
  }

  .page-header__toggle--open {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    background-color: @dark-blue;
  }

  .page-header__wrapper {
    justify-content: flex-start;

    &--index {
      padding-bottom: 7px;
    }
  }

  .page-header__title {
    margin-right: auto;
    margin-top: 62px;
    padding-left: 60px;

    &::after {
      left: 60px;
      height: 8px;
    }

    &--form {
      margin-top: 55px;

      &::after {
        width: 350px;
      }
    }
  }

  .page-header__link {
    margin-top: 54px;
    margin-right: 28px;
  }

  .page-header__logo-image {
    margin-right: 40px;
  }

  .page-header__toggle {
    display: none;
  }

  .page-header-list {
    margin-left: auto;

    .intro-nav__item:nth-child(1),
    .intro-nav__item:nth-child(2) {
      display: block;
    }
  }
}
